.searchbar {
  margin-bottom: auto;
  margin-top: 30%;
  height: 60px;
  background-color: white;
  border-radius: 30px;
  padding: 10px;
  border: 1px solid black;
}

.search_input {
  color: black;
  border: 0;
  outline: 0;
  background: none;
  width: 320px;
  padding: 0 10px;
  /* caret-color:transparent; */
  line-height: 40px;
  /* transition: width 0.4s linear; */
}

